html, body {
  background-color: #000;
  background-image: -webkit-linear-gradient(left, black 0px, rgba(0, 0, 0, 0.6) 200px, rgba(0, 0, 0, 0.6) 1400px, black 1600px), -webkit-linear-gradient(top, black 0px, rgba(0, 0, 0, 0.6) 200px, rgba(0, 0, 0, 0.6) 1000px, black 1200px), url("https://upload.wikimedia.org/wikipedia/commons/1/1f/Drumset_IMG_1235-4_%282476621533%29.jpg");
  background-image: linear-gradient(to right, black 0px, rgba(0, 0, 0, 0.6) 200px, rgba(0, 0, 0, 0.6) 1400px, black 1600px), linear-gradient(to bottom, black 0px, rgba(0, 0, 0, 0.6) 200px, rgba(0, 0, 0, 0.6) 1000px, black 1200px), url("https://upload.wikimedia.org/wikipedia/commons/1/1f/Drumset_IMG_1235-4_%282476621533%29.jpg");
  color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-repeat: no-repeat, no-repeat;
  background-position: center, center;
  background-attachment: fixed, fixed;
  background-size: 1600px 1200px, 1600px 1200px; }

body {
  padding: 0 10px; }

h1 {
  margin: 0;
  padding-top: 10px;
  text-align: center;
  color: #ec8c37;
  font-family: 'Overpass Mono', monospace; }

footer {
  max-width: 1024px;
  margin: auto;
  padding-top: 10px;
  font-family: 'Overpass Mono', monospace; }

.btn {
  line-height: 0;
  padding: 10px;
  background: #D59C6A;
  background: -webkit-linear-gradient(315deg, #D59C6A, #79502D);
  background: linear-gradient(135deg, #D59C6A, #79502D);
  -webkit-transition: all 0.05s ease-in-out;
  transition: all 0.05s ease-in-out;
  color: #000;
  font-weight: 500;
  border: 1px solid #000;
  box-shadow: 1px 1px 2px #000;
  border-radius: 7px; }
  .btn:hover:not(:disabled) {
    background: #ddb088;
    background: -webkit-linear-gradient(315deg, #ddb088, #ab7140);
    background: linear-gradient(135deg, #ddb088, #ab7140); }
  .btn:focus {
    box-shadow: none; }
  .btn:disabled img {
    opacity: 0.2; }

#drum-machine {
  display: grid;
  margin: 0 auto;
  align-items: stretch;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto 1fr auto;
  grid-template-areas: "pads display" "pads recording-top-bar" "pads recording" "volume recording-bottom-bar";
  max-width: 1024px;
  white-space: nowrap; }

#drum-pads {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 1fr;
  border: 0px solid #000;
  border-right-width: 5px;
  grid-area: pads;
  grid-gap: 20px;
  gap: 20px;
  padding: 20px;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #1C4549), to(#69B3BA));
  background: -webkit-linear-gradient(top, #1C4549 5%, #69B3BA 100%);
  background: linear-gradient(to bottom, #1C4549 5%, #69B3BA 100%); }
  #drum-pads::before {
    content: '';
    width: 0;
    padding-bottom: 100%;
    grid-row: 1 / 1;
    grid-column: 1 / 1; }
  #drum-pads *:first-child {
    grid-row: 1 / 1;
    grid-column: 1 / 1; }
  #drum-pads button {
    background: #235E2A;
    background: -webkit-linear-gradient(315deg, #79D784, #235E2A);
    background: linear-gradient(135deg, #79D784, #235E2A);
    color: #ffffff; }
    #drum-pads button:hover {
      background: #389643;
      background: -webkit-linear-gradient(315deg, #94df9d, #389643);
      background: linear-gradient(135deg, #94df9d, #389643); }
    #drum-pads button:focus {
      outline: none;
      box-shadow: 0px 0px 8px #ffffff; }
    #drum-pads button.pressed {
      background: #74cb7f;
      background: -webkit-linear-gradient(315deg, #bcebc2, #74cb7f);
      background: linear-gradient(135deg, #bcebc2, #74cb7f); }

.drum-pad {
  font-size: 1.5em; }

#volume {
  border: 0px solid #000;
  border-right-width: 5px;
  grid-area: volume;
  display: flex;
  align-items: center;
  padding: 0 10px;
  background: #69B3BA; }
  #volume img {
    width: 20px;
    opacity: .75; }
  #volume input {
    margin: 0 10px;
    flex: 1 1 auto; }

#display {
  border: 0px solid #000;
  border-bottom-width: 5px;
  grid-area: display;
  background: #79302D;
  font-size: 1.5em;
  text-align: center; }

#recording-top-bar {
  border: 0px solid #000;
  border-bottom-width: 5px;
  grid-area: recording-top-bar;
  background: #79302D; }

#recording {
  border: 0px solid #000;
  border-bottom-width: 5px;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #79302D), to(#D56F6A));
  background: -webkit-linear-gradient(top, #79302D 5%, #D56F6A 100%);
  background: linear-gradient(to bottom, #79302D 5%, #D56F6A 100%);
  color: #fff;
  cursor: default;
  position: relative;
  grid-area: recording;
  overflow: auto; }
  #recording > div {
    max-height: 100%;
    position: absolute;
    margin: 2px; }
  #recording.state-paused .entry-selected .edit, #recording.state-paused .entry-selected .delete {
    visibility: visible;
    opacity: 1; }

.entry {
  position: relative;
  display: flex;
  -webkit-transition: all 0.05s ease-in-out;
  transition: all 0.05s ease-in-out;
  margin: 0 2px;
  border: 1px solid transparent; }
  .entry .info {
    padding: 0 4px; }
  .entry .edit, .entry .delete {
    line-height: 100%;
    margin: 0;
    margin-left: 5px;
    visibility: hidden;
    opacity: 0;
    padding: 2px 4px;
    -webkit-transition: opacity 0.25s ease-in;
    transition: opacity 0.25s ease-in; }
  .entry.entry-playing .info {
    background: #51201e; }
  .entry.entry-selected .info, .entry.entry-playing .info {
    border: 1px solid #ddd;
    box-shadow: 0px 0px 3px #ddd;
    border-radius: 3px; }

#recording-bottom-bar {
  grid-area: recording-bottom-bar;
  background: #D56F6A; }

.edit-form {
  border: 1px solid #ddd;
  box-shadow: 0px 0px 3px #ddd;
  z-index: 1;
  position: absolute;
  top: 1.75em;
  white-space: pre-wrap;
  background: -webkit-linear-gradient(315deg, #333, #000);
  background: linear-gradient(135deg, #333, #000);
  color: #fff;
  padding: 5px;
  border-radius: 5px; }
  .edit-form label {
    display: block;
    margin: 0; }
  .edit-form select, .edit-form .form-control {
    height: auto;
    padding: 2px;
    display: inline-block;
    min-width: 150px;
    width: 100%; }
  .edit-form .form-group {
    margin-bottom: 10px; }

@media only screen and (max-width: 700px) {
  footer {
    text-align: center; }
  #drum-machine {
    max-width: 400px; }
  #recording {
    min-height: 300px; }
  #drum-pads {
    border-width: 0; }
  #volume {
    padding-bottom: 5px;
    border-width: 0 0 10px 0; }
  #drum-machine {
    grid-template-rows: repeat(autofill, auto);
    grid-template-columns: 1fr;
    grid-template-areas: "pads" "volume" "display" "recording-top-bar" "recording" "recording-bottom-bar"; } }

@media only screen and (max-width: 450px) {
  h1, footer, #display {
    text-align: center;
    font-size: 1em; }
  .record-button {
    padding: 7px; }
  #drum-machine {
    max-width: 300px; }
  #recording {
    min-height: 225px; } }

@media only screen and (max-width: 400px) {
  .record-button {
    padding: 4px; }
  #drum-machine {
    max-width: 250px; }
  #recording {
    min-height: 200px; } }

@media only screen and (max-width: 350px) {
  .record-button {
    padding: 2px; }
  #drum-machine {
    max-width: 200px; }
  #recording {
    min-height: 180px; } }

